body {
  font-family: 'Poppins', sans-serif;
  background-color: #fcfcfc;
}

.chat-area, .home {
  margin-top: 60px;
  position: relative;
}

.chat-area {
  padding: 20px;
  height: 400px;
  overflow-y: scroll;
}

.chat-area .chat-bubble {
  background-color: #bbdefb;
  padding: 20px;
  border-radius: 4px;
  max-width: 300px;
  word-wrap: break-word;
}

.chat-bubble .chat-time {
  font-size: 12px;
  color: #757575;
  text-align: right;
}

.chat-area .chat-bubble.current-user {
  margin-left: auto;
  background-color: #a5d6a7;
}

.form-control {
  width: 60%;
}

.form-control:focus {
  box-shadow: none;
}

.btn {
  margin-top: 10px;
}

.btn-submit {
  background-color: #2e7d32;
  color: white;
}

.updateScore {
  -webkit-animation: MoveUpDown 5s linear 1;
          animation: MoveUpDown 5s linear 1;
  position: absolute;
  left: 50%;
  bottom: 0;
  opacity: 0;
  font-size: 30px;
  color: black;
}
.updateScore.error {
  color: red;
}
.updateScore.success {
  color: greenyellow;
}
@-webkit-keyframes MoveUpDown {
  0% {
    bottom: 60%;
    opacity: 0;
  }
  50% {
    bottom: 75%;
    opacity: 0.5;
  }
  60% {
    bottom: 85%;
    opacity: 0.8;
  }
  80% {
    bottom: 90%;
    opacity: 1;
  }
  100% {
    bottom: 100%;
    opacity: 0;
  }
}
@keyframes MoveUpDown {
  0% {
    bottom: 60%;
    opacity: 0;
  }
  50% {
    bottom: 75%;
    opacity: 0.5;
  }
  60% {
    bottom: 85%;
    opacity: 0.8;
  }
  80% {
    bottom: 90%;
    opacity: 1;
  }
  100% {
    bottom: 100%;
    opacity: 0;
  }
}
.navbar-brand img {
  width: 50px;
  height: 50px;
}
@media screen and (max-width: 767px) {
  .form-control {
    width: 100%;
  }

  .chat-area .chat-bubble {
    max-width: 250px;
  }
}
